import React, { Component } from 'react'
import Flight, { Rect, Oval } from 'react-flight/dom'
import PropTypes from 'prop-types'
import AppBar from './app-bar'

class App extends Component {
  componentDidMount() {}
  render() {
    return (
      <Flight interactive>
        <Flight.Frame duration={300} source>
          <div>

            <AppBar />
            <div
              className="keyframe"
              style={{
                border: 'none',
                backgroundColor: '#F4F7F9',
                height: 280,
              }}
            >
              <Rect
                name={'line1'}
                radius={5}
                style={{
                  backgroundColor: '#95A2AA',
                  left: 120,
                  top: 100 - 40,
                  width: 130,
                  height: 10,
                }}
              />

              <Rect
                name={'line2'}
                radius={5}
                style={{
                  backgroundColor: '#E6ECF0',
                  left: 95,
                  top: 115 - 40,
                  width: 180,
                  height: 10,
                }}
              />

              <Oval
                name="circ1"
                size={16}
                style={{
                  backgroundColor: '#79CD15',
                  left: 110,
                  top: 130 - 40,
                }}
              >
                <div />
              </Oval>

              <Rect
                name="line1-1"
                radius={5}
                style={{
                  backgroundColor: '#E6ECF0',
                  left: 130,
                  top: 133 - 40,
                  width: 50,
                  height: 10,
                }}
              />

              <Oval
                name="circ2"
                size={16}
                style={{
                  backgroundColor: '#FF6D4A',
                  left: 80 + 110,
                  top: 130 - 40,
                }}
              />

              <Rect
                name="line2-1"
                radius={5}
                style={{
                  backgroundColor: '#E6ECF0',
                  left: 80 + 130,
                  top: 133 - 40,
                  width: 50,
                  height: 10,
                }}
              />

              <Rect
                name="line2-2"
                radius={5}
                style={{
                  backgroundColor: '#E6ECF0',
                  left: 50,
                  top: 170 - 40,
                  width: 270,
                  height: 200,
                }}
              />
              <div
                id="brace-1"
                className="brace-1"
                style={{ color: '#95A2AA', top: 30, left: -50, fontSize: 30 }}
              >
                {'{'}
              </div>
              <div
                id="brace-2"
                className="brace-2"
                style={{ color: '#95A2AA', top: 180, left: -50, fontSize: 30 }}
              >
                {'}'}
              </div>
            </div>
          </div>
        </Flight.Frame>

        <Flight.Frame duration={300}>
          <div className="keyframe">
            <Rect
              name={'line1'}
              radius={5}
              style={{
                backgroundColor: '#95A2AA',
                left: 120,
                top: -10,
                width: 130,
                height: 10,
              }}
            />

            <Rect
              name={'line2'}
              radius={5}
              style={{
                backgroundColor: '#E6ECF0',
                left: 400,
                top: 115,
                width: 180,
                height: 10,
              }}
            />

            <Oval
              name="circ1"
              size={22}
              style={{
                backgroundColor: '#79CD15',
                left: 50,
                top: 70,
              }}
            />

            <Rect
              name="line1-1"
              radius={5}
              style={{
                backgroundColor: '#E6ECF0',
                left: 130,
                top: 350,
                width: 50,
                height: 10,
              }}
            />

            <Oval
              name="circ2"
              size={22}
              style={{
                backgroundColor: '#FF6D4A',
                left: 50,
                top: 100,
              }}
            />

            <Rect
              name="line2-1"
              radius={5}
              style={{
                backgroundColor: '#E6ECF0',
                left: 80 + 130,
                top: 350,
                width: 50,
                height: 10,
              }}
            />

            <Rect
              name="line2-2"
              radius={5}
              style={{
                backgroundColor: '#E6ECF0',
                left: 50,
                top: 135,
                width: 50,
                height: 50,
              }}
            />
            <div
              id="brace-1"
              className="brace-1"
              style={{ color: '#95A2AA', top: 25, left: 20, fontSize: 30 }}
            >
              {'{'}
            </div>
            <div
              id="brace-2"
              className="brace-2"
              style={{ color: '#95A2AA', top: 190, left: 20, fontSize: 30 }}
            >
              {'}'}
            </div>
          </div>

        </Flight.Frame>

      </Flight>
    )
  }
}
App.contextTypes = { director: PropTypes.object }

export default App



